.page-main__operating {
  background-color: #1d2631;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  min-height: 200px;
  padding-top: 28px;
  padding-bottom: 30px;
}

@media (min-width: $tablet-width) {
  .page-main__operating {
    position: absolute;
    margin-top: 338px;
    left: -150px;
    width: 100%;
    display: flex;
    background-color: #1d263100;
  }
}

@media (min-width: $desktop-width) {
  .page-main__operating {
    position: absolute;
    margin-top: 409px;
    left: -310px;
  }
}

.page-main__operating-wrap {
  position: relative;
}

.page-main__download {
  color: $white;
  line-height: 18px;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  border: 3px solid #ffffff;
  box-sizing: border-box;
  border-radius: 29px;
  font-weight: 700;
  margin: 0 auto;
  margin-bottom: 19px;
  padding: 15px 0;
  width: 279px;
  height: 54px;
  font-size: 18px;
  position: relative;
  top: 1px;

  &:hover {
    color: $accent;
    background-color: $white;
  }

  &:active {
    color: #d2285534;
    background-color: $white;
  }
}

@media (min-width: $tablet-width) {
  .page-main__download {
    width: 300px;
    margin-top: 13px;
    position: relative;
    right: 11px;
    top: 0;
  }
}

@media (min-width: $desktop-width) {
  .page-main__download {
    width: 300px;
    font-weight: bold;
    font-size: 21px;
    line-height: 18px;
    margin-bottom: 44px;
  }
}

.page-main__operating-system {
  margin-bottom: 20px;
  text-align: center;
  font-size: 0;
  margin-top: 12px;
}

@media (min-width: $tablet-width) {
  .page-main__operating-system {
    margin-top: 23px;
    position: relative;
    right: 12px;
  }
}

@media (min-width: $desktop-width) {
  .page-main__operating-system {
    margin-bottom: 37px;
  }
}

.page-main__apple-logo {
  margin-right: 35px;
  position: relative;
  top: 2px;
}

@media (min-width: $tablet-width) {
  .page-main__apple-logo {
    position: relative;
    top: 1px;
  }
}

.page-main__android-logo {
  margin-right: 35px;
}

.page-main__windows-logo {
  position: relative;
  top: -1px;
}

@media (min-width: $tablet-width) {
  .page-main__windows-logo {
    position: relative;
    top: -2px;
  }
}

@media (min-width: $tablet-width) {
  .page-main__wrap {
    display: flex;
    width: auto;
    height: 729px;
  }
}

.page-main__wrap {
  background-color: #283645;
  width: 100%;
  background-image: url("..//img/background.jpg");
  background-position: center;
  background-size: cover;
  height: 462px;
  background-repeat: no-repeat;

  @media (min-resolution: $retina) {
    background-image: url("..//img/background@2x.jpg");
  }
}

@media (min-width: $tablet-width) {
  .page-main__wrap {
    background-image: url("..//img/background.jpg");
    width: auto;
    height: 729px;
  }
}

@media (min-width: $desktop-width) {
  .page-main__wrap {
    background-image: url("..//img/backdesktop.jpg");
    height: 853px;
    width: 100%;
    background-size: cover;
    background-position: center;

    @media (min-resolution: $retina) {
      background-image: url("..//img/backdesktop@2x.jpg");
    }
  }
}

.page-main__wraper {
  width: 320px;
  margin: 0 auto;
  box-sizing: border-box;
  background-image: url("..//img/phonemobile.png");
  background-position: left bottom;
  height: 462px;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: row;
  justify-content: center;

  @media (min-resolution: $retina) {
    background-image: url("..//img/phonemobile@2x.png");
    background-size: contain;
  }
}


@media (min-width: $tablet-width) {
  .page-main__wraper {
    background-image: url("..//img/phonetablet.png");
    height: 100%;
    width: 660px;

    @media (min-resolution: $retina) {
      background-image: url("..//img/phonetablet@2x.png");
      background-size: contain;
    }
  }
}

@media (min-width: $desktop-width) {
  .page-main__wraper {
    background-image: url("..//img/phonedesctop.png");
    width: 940px;
    margin: 0 auto;
    background-size: 1050px auto;
    background-position: 16px bottom;

    @media (min-resolution: $retina) {
      background-image: url("..//img/phonedesctop@2x.png");
    }
  }
}

.page-main__wraper-title {
  width: auto;
}

@media (min-width: $tablet-width) {
  .page-main__wraper-title {
    width: auto;
  }
}

@media (min-width: $desktop-width) {
  .page-main__wraper-title {
    width: auto;
  }
}

.page-main__img {
  position: relative;
  left: -79px;
  top: 3px;
}

@media (min-width: $tablet-width) {
  .page-main__img {
    position: relative;
    top: 184px;
    width: 593px;
    left: -146px;
    height: 545px;
  }
}

@media (min-width: $desktop-width) {
  .page-main__img {
    position: relative;
    width: 1050px;
    height: 645px;
    top: 208px;
    left: -91px;
  }
}

.page-main__title {
  font-weight: 300;
  color: #ffffff;
  font-size: 24px;
  line-height: 29px;
  margin: 0;
  text-align: center;
  padding-top: 90px;
  padding-bottom: 34px;
  margin-left: 10px;
  margin-right: 10px;
}

@media (min-width: $tablet-width) {
  .page-main__title {
    left: -160px;
    font-weight: 300;
    font-size: 40px;
    line-height: 48px;
    position: relative;
    top: 114px;
    width: 247px;
  }
}

@media (min-width: $desktop-width) {
  .page-main__title {
    font-weight: 300;
    font-size: 50px;
    line-height: 68px;
    position: relative;
    left: -320px;
    top: 119px;
    width: 300px;
  }
}

.page-main__desc {
  color: $white;
  line-height: 24px;
  text-align: center;
  width: 280px;
  margin: 0 auto;
  font-size: 14px;
  font-weight: 300;
  margin-top: 3px;
}

@media (min-width: $tablet-width) {
  .page-main__desc {
    font-weight: 300;
    font-size: 18px;
    line-height: 24px;
    width: 300px;
    text-align: center;
    margin-top: 17px;
    position: relative;
    right: 11px;
  }
}
